button {
  margin: 2px 0px 2px 4px;
}

.login {
  position: absolute;
  top: 45vh;
  left: 45vw;
}

.error { color: #E74C3C ; }


.all-messages {
  height: 400px;
  overflow-y: scroll;
}

.welcome {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sidebar {
  height: 100vh;
  overflow-y: scroll;
}

.users-container {
  width: 29%;
}

.users {
  height: calc(100vh - 35px);
}

.users-filter {
  height: 30px;
}

.user {
  display: flex;
  flex-direction: row;
  height: 70px;
  margin-bottom: 2px;
  border: 1px solid #a9a9a9;
  cursor: pointer;
}

.user:hover {
  /*border: 2px solid #ff4000;*/
  background: #D1F2EB;


}

.user.selected {
  /*border: 1px solid #2E4053;*/
  background: #D6EAF8;
}

