.user-info-descr {
  width: 40%;
}

.user-id {
  font-size: 18px;
  font-weight: bold;
  margin: 2px 5px;
}

.user-status {
  font-size: 12px;
  font-weight: bold;
}

.user-status.important {
  color: #7D3C98;
}

.user-status.ignored {
  color: #CB4335;
}


.user-info-tokens {
  width: 25%;
}

.user-actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 70px;
  width: 35%;
}

.user-lock {
  margin: 2px;
}

.user-lock button {
  margin-left: 5px;
}

.user-lock.locked {
  color: #D35400;
}

.user-lock.locked.lockedByMe {
  color: #6E2C00;
}

/****************** Messages ************************/


.messages {
  width: 69%;
}

.user-msg, .agent-msg {
  display: flex;
}

.user-msg {
  flex-direction: row;
}

.agent-msg {
  flex-direction: row-reverse;
}



.user-msg .msg-item, .agent-msg .msg-item {
  display: flex;
  width: 50%;
  height: 32px;
  padding-top: 10px;
  margin-top: 5px;
}

.msg-item {
  padding: 0px 5px;
}

.user-msg .msg-item {
  flex-direction: row;
  background: #F8F9F9;
}

.agent-msg .msg-item {
  flex-direction: row-reverse;
  justify-content: space-between;
  background: #85C1E9;
}

.msg-time {
  margin: 0px 10px;
  width: 100px;
  color: #2E4053;
}

.agent-msg .msg-item {
  text-align: right;
}

.msg-action {
  /*width: 300px;*/
}

.msg-payload label {
  font-weight: bold;
}

.msg-payload-value {
  margin-left: 20px;
  margin-right: 5px;
  font-family: bold;
}



/**  Agent actions  ***/
.agent-actions {
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #281555;
  background: #FFFAFA;
}

.agent-actions-update-user {
  display: flex;
  flex-direction: row;

}

.agent-action-description {
  width: 50%;
}

.agent-action-status {
  width: 50%;
  text-align: right;
}

.agent-action-description input[type="text"] {
  width: 70%;
}
